import TransitionLink from "gatsby-plugin-transition-link"
// import { Link } from "gatsby";
import PropTypes from "prop-types"
import React, { useState } from "react"
import styled from 'styled-components'
import gsap from "gsap"

const LogoTop = styled.div`
  background: black;
  width: ${({ open }) => (open ? "150px" : "250px")};
  height: ${({ open }) => (open ? "50px" : "90px")};
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  transition: transform 0.5s ease-in-out;
`;

const LogoBottom = styled.div`
  width: ${({ open }) => (open ? "150px" : "245px")};
  height: ${({ open }) => (open ? "50px" : "90px")};
  border: 1px solid black;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  transform: translate(7%, 15%);
  transition: transform 0.5s ease-in-out;
  z-index: -1;
`;

const Logo = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  &:hover {
    > div:first-child {
      transform: translate(-32%, -40%);
    }
    > div:last-child {
      transform: translate(32%, 40%);
    }
  }
`;

const LogoWrapper = styled.header`
  width: ${({ open }) => (open ? "150px" : "300px")};
  height: 175px;  
  position: fixed;
  top: ${({ open }) => (open ? "2%" : "50%")};
  left: ${({ open }) => (open ? "10vw" : "50%")};
  transition: 0.45s ease-in-out;
  transform: ${({ open }) => (open ? "" : "translate(-50%, -70%)")};
`;

const NavWrapper = styled.footer`
  position: fixed;
  top: ${({ open }) => (open ? "90%" : "50%")};
  left: 50%;
  width: 80vw;
  transition: 0.45s ease-in-out;
  transform: ${({ open }) => (open ? "translate(-50%, -50%)" : "translate(-50%, -100%)")};
  z-index: -1;
`;

const NavBody = styled.nav`
  border-bottom: 1px solid black;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
`;

const activeLink = {
  border: `1px solid black`,
  boxShadow: `7px 7px`,
}

const NavLink = styled.div`
  font-size: 22px;
  padding: 0.75em;
  border: 1px solid white;

  transition: 0.2s ease-in;
  &:hover {
    background: grey;
    border: 1px solid grey;
  }
`;

const Copy = styled.p`
  font-size: 12px;
  position: fixed;
  bottom: 0;
  left: 0;
  transform: rotate(90deg) translatey(750%);
  transform-origin: bottom right;
`;

const FadeInGsap = (node, entry, setOpen) => {
  setOpen(true);
  gsap.fromTo(node, {opacity: 0}, {opacity: 1, duration: entry.length})
}

const Navbar = ({ isHome }) => {
  // if loaded on home page, close nav
  // else open nav
  const [open, setOpen] = useState(!isHome);
  const ENTRY_LENGTH = 0.4;
  const EXIT_LENGTH = 0.4;
  return (
  <>
  <TransitionLink
    to="/"
  >
    {/* {siteTitle} */}
    <LogoWrapper open={open} onClick={()=>setOpen(false)}>
      <Logo>
        <LogoTop open={open}>
          <h2 style={{color: `white`}}>Hilman</h2>
        </LogoTop>              
        <LogoBottom open={open}>            
          <h2>Azfar</h2>
        </LogoBottom>
      </Logo>
    </LogoWrapper>
  </TransitionLink>      

  <NavWrapper open={open}>
    <NavBody>
      
      <TransitionLink 
        to="/about/" 
        activeStyle={activeLink}
        exit={{
          length: EXIT_LENGTH,
          trigger: ({node, exit}) => gsap.fromTo(node, {opacity: 1}, {opacity: 0, duration: exit.length})
        }}
        entry={{
          length: ENTRY_LENGTH,
          trigger: ({node, entry}) => FadeInGsap(node, entry, setOpen)
        }}
      >
        <NavLink>About</NavLink>
      </TransitionLink>
      
      <TransitionLink 
        to="/projects/" 
        activeStyle={activeLink}
        exit={{
          length: EXIT_LENGTH,
          trigger: ({node, exit}) => gsap.fromTo(node, {opacity: 1}, {opacity: 0, duration: exit.length})
        }}
        entry={{
          length: ENTRY_LENGTH,
          trigger: ({node, entry}) => FadeInGsap(node, entry, setOpen)
        }}
      >
        <NavLink onClick={()=>setOpen(true)}>Projects</NavLink>
      </TransitionLink>
      
      <NavLink/>
      <NavLink/>
      
      <TransitionLink 
        to="/resume/" 
        activeStyle={activeLink}
        exit={{
          length: EXIT_LENGTH,
          trigger: ({node, exit}) => gsap.fromTo(node, {opacity: 1}, {opacity: 0, duration: exit.length})
        }}
        entry={{
          length: ENTRY_LENGTH,
          trigger: ({node, entry}) => FadeInGsap(node, entry, setOpen)
        }}
      >
        <NavLink onClick={()=>setOpen(true)}>Resume</NavLink>
      </TransitionLink>
      
      <TransitionLink 
        to="/contact/" 
        activeStyle={activeLink}
        exit={{
          length: EXIT_LENGTH,
          trigger: ({node, exit}) => gsap.fromTo(node, {opacity: 1}, {opacity: 0, duration: exit.length})
        }}
        entry={{
          length: ENTRY_LENGTH,
          trigger: ({node, entry}) => FadeInGsap(node, entry, setOpen)
        }}
      >
        <NavLink onClick={()=>setOpen(true)}>Contact</NavLink>
      </TransitionLink>
      
    </NavBody>
  </NavWrapper>

  <Copy> Hilman 2020 | Made with Gatsby</Copy>
  </>
  )
}

Navbar.propTypes = {
  siteTitle: PropTypes.string,
  isHome: PropTypes.bool,
}

Navbar.defaultProps = {
  siteTitle: ``,
  isHome: true,
}

export default Navbar
